import React from 'react';
import Tog from './toggler.png';
import Main from './main';
import About from './about';
import Collection from './collection';
import Contact from './contact';


const Elements = [
{
  title: "home",
  description: <Main/>
},
{	
  title: "About" ,
  description: <About/>
},
{
  title: "Collections",
  description: <Collection/>
},
{
  title: "Contact",
  description: <Contact/>
}
];

export default class Sidebar extends React.Component{
constructor(props){
super(props);
this.state = { index:0, show:true };
}




handleClick = () =>{		
this.setState(prevState => {
       return {index: prevState.index + 1};
       
    });

}

render(){
return (

<div className="flex">
<div className="sidebar">

<img className="togg" src={Tog} onClick={ this.handleClick.bind(this) } hidden={ this.state.index==3 ? this.state.show:'' }/>
<h1>P</h1>
<p><small>Prakash Thapa</small></p>
<div className={Elements[this.state.index].title }>
<strong className="navcolor">{Elements[this.state.index].title }</strong>
</div>
</div>

{Elements[this.state.index].description }

</div>
);
}	
	


}